.modal {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
}
.modal.is-visible {
  visibility: visible;
}
.modal-overlay {
  position: fixed;
  z-index: 10;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s linear 0.3s, opacity 0.3s;
}
.modal.is-visible .modal-overlay {
  opacity: 1;
  visibility: visible;
  transition-delay: 0s;
}
.modal-wrapper {
  position: absolute;
  z-index: 10;
  width: 500px;
  background-color: #fff;
}
.modal-transition {
  transition: all 0.3s 0.12s;
  transform: translateY(-10%);
  opacity: 0;
}
.modal.is-visible .modal-transition {
  transform: translateY(0);
  opacity: 1;
}
.modal-header, .modal-content {
  padding: 1em;
}
.modal-header {
  position: relative;
}
.modal-header {
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
  flex-direction: column;
  padding: 40px 15px;
  border: none;
  border-radius: 0;
  color: #040707;
  font-weight: 600;
}
.modal-close {
  position: absolute;
  top: 0;
  right: 0;
  color: #fff;
  background: none;
  border: 0;
  background: red;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
.modal-close svg.svg-inline--fa.fa-w-12 {
  width: 40px;
  height: 40px;
  transition: all 0.5s;
}
.modal-close:hover svg {
  transform: rotate(180deg);
}
.modal-heading {
  width: 100%;
  font-size: 30px;
  text-transform: uppercase;
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
}
.modal-body {
  padding: 0;
}
.modal-header {
  padding-bottom: 0;
}

.modal-content {
  background: transparent;
  color: #040707;
  border: none;
  border-radius: 0;
  font-weight: 500;
}
.modal-header__img {
  margin: 40px auto 20px auto;
}
@media (max-width: 768px) {
  .modal-wrapper {
    width: 95%;
    margin-left: auto;
    margin-right: auto;
  }
  .modal-heading {
    font-size: 20px;
  }
}
@media(max-width: 767px) {
  .modal.is-visible .modal-transition {
    height: 90vh;
    overflow-y: scroll;
  }
}
